import React, { useContext, useState, useEffect } from 'react'
import { SidebarContext } from '../context/SidebarContext'
import {
  ContractIcon,
  MoonIcon,
  SunIcon,
  MenuIcon,
} from '../icons'
import { Input, WindmillContext, Button } from '@windmill/react-ui'
import { ethers } from 'ethers'
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";

const getVanity = (x) => {
  let charCount = 4;
  let userFirst = x.substring(0, charCount);
  let userLast = x.slice(-charCount);
  let userVanity = `${userFirst}...${userLast}`;
  return userVanity
  }
function Header({ address, setAddress, providerAddress, connectWallet, setProviderAddress }) {
  const { mode, toggleMode } = useContext(WindmillContext)
  const { toggleSidebar } = useContext(SidebarContext)
  //const [ providerAddress, setProviderAddress ] = useState(null);
  const [ mainProvider, setMainProvider ] = useState(null);
  //~~~~
  try{
  window.ethereum.on('chainChanged', function (chainID) {
    return connectWallet()
  })
  window.ethereum.on('accountsChanged', function (accounts) {
  return connectWallet()
  })
}catch(err){
  console.log(err)
}
  //~~~~
  async function disconnectWallet(){
    console.log(mainProvider)
    //await mainProvider.disconnect()
    setProviderAddress(null);
  }

  useEffect(() => {
    setAddress('')
    setProviderAddress(null)
    connectWallet()
    }, []);
  return (
    <header className="z-40 py-4 bg-white shadow-bottom dark:bg-gray-900">
      <div className="container flex items-center justify-between h-full px-6 mx-auto text-blue-300 dark:text-blue-300">
        {/* <!-- Mobile hamburger --> */}
        <button
          className="p-1 mr-5 -ml-1 rounded-md lg:hidden focus:outline-none focus:shadow-outline-blue"
          onClick={toggleSidebar}
          aria-label="Menu"
        >
          <MenuIcon className="w-6 h-6" aria-hidden="true" />
        </button>
        {/* <!-- Search input --> */}
        <div className="flex justify-center flex-1 lg:mr-32">
          <div className="relative w-full max-w-xl mr-6 focus-within:text-blue-300">
            <div className="absolute inset-y-0 flex items-center pl-2">
              <ContractIcon className="w-4 h-4" aria-hidden="true" />
            </div>
            <Input
              className="pl-8 text-gray-700"
              placeholder="Paste your address here"
              aria-label="Search"
              value={address}
              onChange={e => {
                setAddress(e.target.value)}
              }
            />
          </div>
        </div>
        <ul className="flex items-center flex-shrink-0 space-x-6">
          {/* <!-- Theme toggler --> */}
          <li className="flex">
            <button
              className="rounded-md focus:outline-none focus:shadow-outline-blue"
              onClick={toggleMode}
              aria-label="Toggle color mode"
            >
              {mode === 'dark' ? (
                <SunIcon className="w-5 h-5" aria-hidden="true" />
              ) : (
                <MoonIcon className="w-5 h-5" aria-hidden="true" />
              )}
            </button>
          </li>
          <li className="flex">
          {providerAddress ? 
            <Button  className="mt-6 mb-6"> {getVanity(providerAddress)}</Button>
              : <Button onClick={connectWallet} className="mt-6 mb-6" > CONNECT YOUR WALLET </Button>
              }
          </li>
        </ul>
      </div>
    </header>
  )
}

export default Header
